:root {
  --canopy-green: #014038;
  --meadow-mist: #96BAA0;
  --sky-bloom: #BDD4DA;
  --sunlit-petal: #F0C06D;
  --cloudy-grey: #F7F7F7;
  --willow-gray: #C5C3C3;
}

/* Example:
h1 {
  font-size: rem(30);  <-- this is 1.875rem
}
*/
:root {
  /* Title Large */
  --font-titleLarge: "Arial", sans-serif;
  --size-titleLarge: 30px;
  --weight-titleLarge: bold;
  /* Title */
  --font-title: "Arial", sans-serif;
  --size-title: 25px;
  --weight-title: normal;
  /* Subtitle */
  --font-subtitle: "Comfortaa", sans-serif;
  --size-subtitle: 15px;
  --weight-subtitle: normal;
  /* Body Large */
  --font-bodyLarge: "Calibri", sans-serif;
  --size-bodyLarge: 20px;
  --weight-bodyLarge: normal;
  /* Body Strong */
  --font-bodyStrong: "Calibri", sans-serif;
  --size-bodyStrong: 16px;
  --weight-bodyStrong: bold;
  /* Body */
  --font-body: "Calibri", sans-serif;
  --size-body: 16px;
  --weight-body: normal;
  /* Captions */
  --font-captions: "Arial", sans-serif;
  --size-captions: 11px;
  --weight-captions: normal;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  text-decoration: none;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background: var(--canopy-green);
  color: var(--willow-gray);
}

main {
  flex: 1;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 69.375em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.titleLarge {
  font: var(--weight-titleLarge) var(--size-titleLarge) var(--font-titleLarge);
}

.title {
  font: var(--weight-title) var(--size-title) var(--font-title);
}

.subtitle {
  font: var(--weight-subtitle) var(--size-subtitle) var(--font-subtitle);
}

.bodyLarge {
  font: var(--weight-bodyLarge) var(--size-bodyLarge) var(--font-bodyLarge);
}

.bodyStrong {
  font: var(--weight-bodyStrong) var(--size-bodyStrong) var(--font-bodyStrong);
}

.body {
  font: var(--weight-body) var(--size-body) var(--font-body);
}

.captions {
  font: var(--weight-captions) var(--size-captions) var(--font-captions);
}

.container {
  padding: 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 54.375em) {
  .container {
    padding: 0;
  }
}

.homeContent {
  display: flex;
  text-align: center;
  justify-content: space-around;
}
@media (min-width: 54.375em) {
  .homeContent {
    display: flex;
    justify-content: space-around;
  }
}
.homeContent__categories, .homeContent__links {
  display: flex;
  flex-direction: column;
}
.homeContent__products, .homeContent__reviews {
  display: flex;
  flex-wrap: wrap;
  width: 66%;
}
.homeContent__stuffBox {
  margin: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--cloudy-grey);
  padding: 0.625rem;
  border: 0rem;
  border-radius: 0.9375rem;
}
@media (max-width: 54.3125em) {
  .homeContent {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
  }
  .homeContent__categories, .homeContent__products, .homeContent__reviews, .homeContent__links {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    width: 100%;
  }
}

.category,
.link {
  display: block;
  background-color: var(--meadow-mist);
  padding: 1.25rem;
  margin-top: 0.3125rem;
  margin-bottom: 0.3125rem;
  border: 0.1875rem solid var(--sky-bloom);
  transition: all 0.5s;
  border-radius: 0.9375rem;
  position: center;
}

.product,
.review {
  display: inline-block;
  margin-bottom: 1.875rem;
  background-color: var(--meadow-mist);
  border: 0.1875rem solid var(--sky-bloom);
  padding: 0.625rem;
  border-radius: 0.9375rem;
  width: 18.75rem;
  height: 21.875rem;
}

.category:hover,
.product:hover,
.review:hover,
.link:hover {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  border-color: var(--sunlit-petal);
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  transform: scale(1.01);
  transition: all 0.3s ease-out;
}

.card {
  padding: 1.25rem;
}

.footer {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  padding: 0.9375rem 6.25rem;
  display: flex;
  justify-content: space-around;
  width: 100%;
  min-height: auto3.125rem;
}
@media (min-width: 54.375em) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.footer p {
  margin: 0.625rem 0 0.625rem;
  text-align: start;
}
@media (max-width: 54.3125em) {
  .footer {
    flex-direction: column;
    padding: 0.625rem 0;
    align-items: center;
    text-align: center;
  }
  .footer p {
    text-align: center;
  }
}
.footer__logo {
  margin: 0 auto;
}
.footer__title {
  margin-top: 0.3125rem;
}
.footer__copywrite {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 1.25rem auto 0;
  text-align: center;
}
.footer__copywrite p {
  margin: 0;
}
.footer__first--coDetails, .footer__second--company, .footer__third--quickLinks, .footer__fourth--socials {
  display: flex;
  flex-direction: column;
}
.footer__socialsTop {
  margin-bottom: 0.625rem;
  gap: 0.9375rem;
}
.footer__socialsBottom {
  margin-top: 0.625rem;
  margin-bottom: 2.5rem;
}
.footer__socialLinks a:link {
  color: var(--canopy-green);
}
.footer__socialLinks a:visited {
  color: var(--canopy-green);
}
.footer__socialLinks a:hover {
  color: var(--meadow-mist);
}
.footer__socialLinks a:active {
  color: var(--sunlit-petal);
}

.header {
  background-color: var(--sky-bloom);
  color: var(--canopy-green);
  padding: 15px 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 54.375em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 54.3125em) {
  .header {
    padding: 0.625rem 0;
  }
}
.header__hamburger {
  display: flex;
  align-items: center;
  margin-left: 0.625rem;
}
.header__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
}
.header__search, .header__searchbar, .header__shoppingcart, .header__account {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: auto;
  margin-right: 0.625rem;
}
.header__search {
  margin-right: 0.125rem;
}
.header input[type=text] {
  padding-left: 0.625rem;
  box-sizing: border-box;
  border: 0 solid var(--sky-bloom);
  height: 1.875rem;
  border-radius: 0.9375rem;
  color: var(--canopy-green);
  background-color: var(--meadow-mist);
}
@media (max-width: 54.3125em) {
  .header input[type=text],
  .header .header__account {
    display: none;
  }
}
.header__upper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header__left {
  display: flex;
  justify-content: flex-start;
  flex: 1;
}
.header__center {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0.625rem;
  margin: 0 auto;
  text-align: center;
  position: center;
}
.header__right {
  display: flex;
  justify-content: flex-end;
  flex: 1;
}
.header__lower {
  display: block;
  position: absolute;
  top: calc(100% - 1.5rem);
  width: 100%;
}